export default {
  state: () => ({
    isShowHeader: false
  }),
  mutations: {
    TOGGLE_STATE(state, payload) {
      state[payload] = !state[payload];
    }
  },
  actions: {
    togglePanel({ commit, payload }) {
      commit("TOGGLE_STATE", payload);
    }
  },
  modules: {},
  getters: {
    getStateProp: state => prop => state[prop]
  }
};
