import axios from "axios";
import router from "../router";

const BASE_URI = "https://ideao-api.eu-gb.cf.appdomain.cloud";

export default {
  state: () => ({
    questions: [],
    errors: [],
    selcted: {},
  }),
  mutations: {
    GET_QUESTIONS(state, payload) {
      state.questions = payload;
    },
    SET_SELECTED(state, payload) {
      state.selcted = payload;
    },
    PUSH_ERRORS(state, payload) {
      state.errors.push(payload);
    },
  },
  actions: {
    fetchQuestions({ commit }) {
      try {
        axios
          .get(`${BASE_URI}/api/questions`)
          .then((res) =>
            axios.all(
              res.data.rows.map(({ id }) =>
                axios
                  .get(`${BASE_URI}/api/questions/${id}`, {
                    params: { id },
                  })
                  .then((res) => res.data)
              )
            )
          )
          .then((res) => commit("GET_QUESTIONS", res));
      } catch (e) {
        commit("PUSH_ERRORS", e);
      }
    },

    setSelected({ commit }, question) {
      commit("SET_SELECTED", question);
    },

    postSolution({ commit }, question) {
      try {
        axios.put(`${BASE_URI}/api/questions/${question._id}`, { question });
      } catch (e) {
        commit("PUSH_ERRORS", e);
      }
    },

    login({ commit }) {
      try {
        axios
          .get(`${BASE_URI}/appid/login`)
          .then(() => router.push({ name: "Questions" }).catch(() => null));
      } catch (e) {
        commit("PUSH_ERRORS", e);
      }
    },
  },
  modules: {},
  getters: {
    getQuestions: (state) => state.questions,
    getSelected: (state) => state.selected,
    getErrors: (state) => state.errors,
  },
};
